___CSS_LOADER_EXPORT___.push([module.id, `#seven-days-forecast {
    display: grid;
    grid-template-columns: 0.25fr repeat(7, 1fr) 0.25fr;
    grid-template-rows: auto auto;
    /* grid-auto-flow: row; */
    row-gap: 1rem;

}

.icon-condition>img {
    width: 4rem;
}

.day {
    padding: 0 0 0 1.5rem;

    grid-row: 2/3;

    display: flex;
    flex-direction: column;
    gap: 0.75rem;

    font-size: 0.65rem;
}

.day-1 {
    grid-column: 2;
}

.day-2 {
    grid-column: 3;
}

.day-3 {
    grid-column: 4;
}

.day-4 {
    grid-column: 5;
}

.day-5 {
    grid-column: 6;
}

.day-6 {
    grid-column: 7;
}

.day-7 {
    grid-column: 8;
}

.separator {
    grid-row: 1/2;
    grid-column: 1/-1;
    border: 0.5px solid rgba(0, 0, 0, 0.5);
}

.today-separator-mark {
    grid-row: 1/2;
    grid-column: 2/3;
    z-index: 1;
    border: 1px solid #fbbf24;
}

.week-day {
    font-size: 1rem;
    font-weight: 500;
}

.day-date, .min-max-temp, .icon-condition {
    display: flex;
    flex-direction: column;
    
    line-height: 1.1;
}

.min .seven-days-min-max-data,
.max .seven-days-min-max-data {
    font-weight: 600;
}


.icon-condition {
    gap: 0.25rem;
}

.icon-condition .condition {
    font-weight: 600;
}
